.cmp-adaptiveform-button {
  border: none;
  flex-direction: column;
  margin: 12px 0;
  display: flex;
  position: relative;
}

.cmp-adaptiveform-button[data-cmp-valid="false"] input, .cmp-adaptiveform-button[data-cmp-valid="false"] textarea, .cmp-adaptiveform-button[data-cmp-valid="false"] select {
  background: #eb3c360d;
  border: 2px solid #eb3c36;
  outline-color: #eb3c36;
}

.cmp-adaptiveform-button[data-cmp-valid="false"] > .cmp-adaptiveform-button__shortdescription {
  display: none;
}

.cmp-adaptiveform-button[data-cmp-valid="false"] > .cmp-adaptiveform-button__errormessage {
  align-items: center;
  display: flex;
}

.cmp-adaptiveform-button[data-cmp-required="true"] .cmp-adaptiveform-button__label {
  -webkit-column-gap: 4px;
  -moz-column-gap: 4px;
  justify-content: flex-start;
  column-gap: 4px;
  display: flex;
}

.cmp-adaptiveform-button[data-cmp-required="true"] .cmp-adaptiveform-button__label:after {
  content: "*";
  color: #eb3c36;
  font-weight: bold;
}

.cmp-adaptiveform-button.cmp-tabs, .cmp-adaptiveform-button.cmp-adaptiveform-wizard, .cmp-adaptiveform-button.cmp-accordion {
  margin: 72px 0;
}

.cmp-adaptiveform-button__widget {
  width: 100%;
  height: 48px;
  cursor: pointer;
  color: #fff;
  background: #161616;
  border: none;
  border-radius: 500px;
  padding: 0 36px;
  font-family: defaultFont, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 600;
  display: block;
}

.cmp-adaptiveform-button__widget:hover:not([disabled]) {
  transition: box-shadow .1s ease-out, transform .1s ease-out, -webkit-box-shadow .1s ease-out, -webkit-transform .1s ease-out;
  position: relative;
  transform: translateY(-5%)scale3d(1.02, 1.02, 1);
  box-shadow: 0 6px 12px 1px #00000040;
}

.cmp-adaptiveform-button__widget:disabled {
  cursor: default;
  background-color: #eee;
}

.cmp-adaptiveform-button__widget[data-cmp-enabled]:not([data-cmp-enabled="true"]) {
  color: #ddd;
  background: #6d6d6d;
}

@media (min-width: 640px) {
  .cmp-adaptiveform-button__widget {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
}

.cmp-adaptiveform-button__longdescription {
  opacity: 1;
  color: #161616;
  pointer-events: none;
  -webkit-column-gap: 12px;
  -moz-column-gap: 12px;
  background-color: #eee;
  border-radius: 6px;
  align-items: flex-start;
  column-gap: 12px;
  margin: 4px 0;
  padding: 12px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.cmp-adaptiveform-button__longdescription:before {
  content: "";
  height: 16px;
  min-width: 16px;
  background: url("resources/images/long-description.svg") center / cover no-repeat;
}

.cmp-adaptiveform-button__longdescription p {
  margin: 0;
}

.cmp-adaptiveform-button__shortdescription {
  color: #6d6d6d;
  margin: 0 0 12px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  position: static;
}

.cmp-adaptiveform-button__questionmark {
  top: 0;
  width: 16px;
  height: 16px;
  cursor: pointer;
  background: url("resources/images/question.svg") center / cover no-repeat;
  border: none;
  border-radius: 9px;
  padding: 0;
  display: flex;
  position: absolute;
  inset-inline-end: 0;
}

.cmp-adaptiveform-button__label-container {
  justify-content: space-between;
  display: flex;
}

.cmp-adaptiveform-button__label-container > button[class*="__questionmark"] {
  position: unset;
  inset-inline-end: unset;
}

.cmp-adaptiveform-button__shortdescription, .cmp-adaptiveform-button__longdescription {
  margin-top: 8px;
}

@media (max-width: 640px) {
  .cmp-adaptiveform-button__questionmark {
    top: -20px;
  }

  .cmp-adaptiveform-button {
    margin-top: 32px;
  }
}

